import axios, { AxiosError } from 'axios';
import config from '../constants/Config';
import { dispatch } from '../rematch/store';
import { HMController } from '../afi-manager-base-model/controllers/HMController';
import { LocalStoryController } from './LocalStoryController';
import { OrderTrackingController } from './OrderTrackingController';
import { TestController } from './TestController';
import { UserController } from './UserController';
import { routersMap, TypeScreen } from 'src/constants/Route';
import { MetaDataController } from './MetaDataController';
import { UserHmController } from './UserHmController';
import PermissionContainer from 'src/container/permission/PermissionContainer';
import { PermissionController } from './PermissionController';
import { RoleController } from './RoleController';
import { MenuTemplateController } from './MenuTemplateController';
import { ExcelController } from './ExcelController';

export const appClient = axios.create({
    baseURL: config.apiGatewayUrl,
    timeout: 30000,
    headers: {
        common: {
            'Content-Type': 'application/json',
        },
    },
});
appClient.interceptors.request.use(
    (res) => {
        dispatch.loading.showLoading();
        return res;
    },
    (err: AxiosError) => { },
);

appClient.interceptors.response.use(
    (res) => {
        dispatch.loading.hiddenLoading();
        return res;
    },
    (err: AxiosError) => {
        if (err.message === 'Network Error') {
            dispatch.notification.error('Li kt ni my ch');
            // window.location.href = "network-error"
        }
        if (err.response?.status === 401) {
            const linkNow = window.location.pathname;
            const getRoute = routersMap.get(linkNow);
            if (getRoute && getRoute?.typeAuthen != TypeScreen.public) {
                dispatch.notification.error('Authentication error, please login again');
                window.location.href = '/login';
            }
        } else if (err.response?.status === 403) {
            dispatch.notification.error('Bn hng c quyn i hnh ng ny u ');
        } else {
            dispatch.notification.error(err.response?.data?.message || ' C li xy ra');
        }
        dispatch.loading.hiddenLoading();
        return Promise.reject(err);
    },
);
appClient.defaults.headers['authorization'] =
    'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..8YaNLr8SVimJq9LCrURn6nBbXEjMpzgFM2Xs_2O97h0';
export const localStoryController = new LocalStoryController();

export const userController = new UserController(config.apiGatewayUrl, 'user', appClient);

export const testController = new TestController('https://apidev.tracking.afivn.com', 'appUser', appClient);
export const orderTrackingController = new OrderTrackingController(config.apiGatewayUrl, 'orderTracking', appClient);

export const metaDataController = new MetaDataController(config.apiGatewayUrl, 'metaData', appClient);

export const hMController = new HMController('https://api.parcellab.com/v2', '');

export const userHmController = new UserHmController(config.apiGatewayUrl, 'afi/userHm', appClient);

export const permssionController = new PermissionController(config.apiGatewayUrl, 'permssion', appClient);
export const roleController = new RoleController(config.apiGatewayUrl, 'role', appClient);
export const menuTeamplateController = new MenuTemplateController(config.apiGatewayUrl, 'menuTemplate', appClient);
export const excelController = new ExcelController(appClient);

