import { BaseModel } from 'luong-base-model/lib';
import { HMTracking } from './HMTracking';
import { ProductHM } from './ProductHM';
import { UserHm } from './UserHm';

export class OrderTracking extends BaseModel {
    orderSource?: EOrderSource;

    orderId?: string;

    trackingId?: string;

    userId?: string;

    customerName?: string;

    desc?: string;

    infoHM?: HMTracking;

    status?: EStatusOrderTracking;

    lastTimeTracking?: Date;

    email?: string;

    productInfo?: ProductHM[];

    productLink?: string[];

    userHM?: UserHm;

    userHMId?: string;

    totalPrice?: number;
    totalPriceBuy?: number;

    productOrder?: ProductOrder[];
    isOrder?: boolean;
    isRegister?: boolean;
    errorDesc?: string;

    dataFirebase?: DataFirebaseHm;

    registerByName?: string;
    addCartByName?: string;
    logs?: string[];
}

export interface ProductOrder {
    id?: string;
    productId?: string;
    size?: string;
    price?: number;
    originPrice?: number;
    buyPrice?: number;
    quantity?: number;
    errorDesc?: string;
    name?: string;
    img?: string;
}

export interface DataFirebaseHm {
    email?: string;
    timestamp?: string;
    total?: string;
    username?: string;
    orderId?: string;
    address?: BillingArress;
    giftCards?: Giftcard[];
}

export enum EOrderSource {
    HM = 'H&M',
    botAutoHm = 'botAutoHm',
    autoSplit = 'autoSplit',
}

export enum EStatusOrderTracking {
    COMPLETED = 'Complete',
    FLOWING = 'Flowing',
}

interface BillingArress {
    validated: boolean;
    firstName: string;
    lastName: string;
    cellPhone: string;
    
    line1: string;
    line2: string;
    postalCode: string;
    town: string;
  }

  interface Giftcard {
    balanceAmount: BalanceAmount;
    remainAmount?: any;
    cardNumber: string;
    cardPin?: any;
    eovDate?: any;
    currency?: any;
    invoiceNumber?: any;
    deductedAmount: BalanceAmount;
    returnCode?: any;
    displayMessage?: any;
  }
  
  interface BalanceAmount {
    currencyIso: string;
    value: number;
    priceType: string;
    formattedValue: string;
    minQuantity?: any;
    maxQuantity?: any;
    type?: any;
  }